<template>
    <div class="container">

        <form action="" @submit.prevent="registerVencimento">


<article>
    <h4> Produto</h4>
    <div class="quarter-width">
  <label for="fieldCodigo">Código</label>
    <input type="text" v-model="vencimento.produto.codigo" required placeholder="78954654654" id="fieldCodigo">
    </div>

<div class="three-quarter-width ">

    <label for="fieldNome">Nome</label>
    <input type="text" v-model="vencimento.produto.nome" :disabled="vencimento.produto.id" placeholder="Sabão Minuano" id="fieldNome" required>
</div>
<div v-if="!vencimento.produto.id" class="full-width">
<label for="fieldSetor">Setor</label>
    <select id="fieldSetor" required v-model="vencimento.produto.setor">
      <option v-for="setor in setores" v-bind:key="setor.id" :value="setor.id">{{setor.nome}}</option>
    </select>
</div>

</article>  
<article>
    <h4> Vencimento</h4>

    <div class="half-width">
  <label for="fieldVencimento">Validade</label>
    <input type="date" placeholder="CJ Patoilo" id="fieldVencimento" required v-model="vencimento.produto.data">

    </div>


    <div class="half-width">

    <label for="fieldQuantidade">Quantidade</label>
    <input type="number" placeholder="Unidades, ex: 1, 4, 7, 9" id="fieldQuantidade" required v-model="vencimento.produto.quantidade">
    </div>



</article>
 

        <button class="button"> Salvar </button>
        </form>

    </div>

</template>

<script>
    export default {
        name: 'Vencimento',

        mounted() {
            if (localStorage.players) this.players = JSON.parse(localStorage.players);
            if (localStorage.rodadaAtual) this.rodadaAtual = JSON.parse(localStorage.rodadaAtual);
        },
        data() {
            return {
                players: [],
                vencimento: {
                    produto: {}
                },
                rodadaAtual: 1,
                vencedor: null,
                showVencedor: false,
                finalizar: false, 
                setores :[
                    {id:1, nome:'Deposito'},
                    {id:2, nome:'Freezer'},
                    {id:3, nome:'Material de Limpeza'},
                ]
            }
        },
        computed : {
            ended: function() {
                let withPoints = this.players.filter(p=> p.points>0);
                return  withPoints.length > 1 ? false : withPoints[0]; 
            }
        },
        methods: {
            registerVencimento : function(evt) {
console.log(evt)
            },
            updatePoints: function(item) {
                this.players.forEach(p => {
                    if (p.points > 0) {
                        if (p.uniqueId == item.uniqueId) {
                            return;
                        }
                        if (p.playing) {
                            p.points = p.points > 1 ? p.points - 2 : 0;
                        } else {
                            p.points--;
                        }
                    }

                    p.playing = true;
                });
            },
            setWinner: function(item) {

                this.finalizar = false;
                this.updatePoints(item);
                localStorage.players = JSON.stringify(this.players);
                this.rodadaAtual++;
                localStorage.rodadaAtual = JSON.stringify(this.rodadaAtual);
            },
            showWinnerButton: function() {
                this.finalizar = true;
            },
            restart: function() {
                this.rodadaAtual = 0;
                this.players.forEach(p => {
                        p.playing = true;
                        p.points = 10;
                    });
                localStorage.players = JSON.stringify(this.players);
                localStorage.rodadaAtual = JSON.stringify(this.rodadaAtual);
                }
            }
    }
</script>

